@import url('https://fonts.googleapis.com/css2?family=Prompt:wght@200;500&display=swap');

html, body {
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: 'Prompt', sans-serif;
}

main {
  height: 100vh;
  overflow: hidden;
  scroll-behavior: auto;
  scroll-snap-type: y mandatory;
  > section {
    height: 100vh;
    scroll-snap-align: start;
    position: relative;
  }
}

#badwords {
  background-color: rgb(14, 1, 24);
  .ball {
    width: 70px;
    height: 70px;
    background-color: #f7f7f7;
    border-radius: 1000000px;
    position: absolute;
    transform-origin: center center;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    font-size: 0.9rem;
    box-shadow: 0 0 15px 10px rgba(10, 10, 10, 0.352);
    user-select: none;
  }
}

#detail {
  background-color: #f7f7f7;
  z-index: 8;
  .container {
    max-width: 500px;
    padding: 3rem 1rem;
    margin: auto;
    p {
      font-weight: lighter;
      margin-block-start: 1.5em;
      margin-block-end: 1.5em;
    }
    .noun {
      font-weight: 300;
      font-size: 0.8rem;
      margin-left: 5px;
    }
  }
}

.relative { position: relative; }
.absolute { position: absolute; }
.top-0 { top: 0; } 
.right-0 { right: 0;} 
.close {
  border: none;
  background-color: transparent;
  font-family: inherit;
  text-decoration: underline;
  font-weight: lighter;
  height: 32px;
  max-height: 32px;
  max-width: 32px;
  min-height: 32px;
  min-width: 32px;
  width: 32px;
  &::before,
  &::after {
    background-color: #000;
    content: "";
    display: block;
    left: 50%;
    position: absolute;
    top: 50%;
    transform-origin: center center;
    height: 2px;
    width: 50%;
  }
  &::before {
    transform: translateX(-50%) translateY(-50%) rotate(45deg);
  }
  &::after {
    transform: translateX(-50%) translateY(-50%) rotate(-45deg);
  }
}
